li{
    list-style: none;
}
html,body,#root,.App{
     height: 100%;
}
.menu{
    display: flex;
    background: #5e93dc
}
.menu a{
    /* margin: 0 20px; */
    padding: 20px;
    color: #fff;
    /* border: none; */
    text-decoration: none;
    /* justify-content: ; */
}
.menu div a{
    height: 100%;
}
.menu div {
    position: relative;
    height: 60px;
    top: 20px;
}
.menu div:hover ul{
    display:block;
}
.menu div ul{
    display: none;
    z-index: 9999;
    padding: 0px!important;
}
.menu div ul{
    background: #777;
}
.menu div ul li{
    padding: 15px 0 !important;
    display: flex;
    justify-content: center;
}
.menu div ul li a{
    padding: 0!important;
    text-align: center;
}
.menu a:hover{
    color: #333
}
.menu .act{
    color: #f8710d;
    background: #F4F4F4;
}
.b_c{
    border: 1px solid #999;
    width: 95%;
    margin: 20px auto;
    height: 100%;
    padding:  20px;
    /* border-top: none; */
}
.b_c a {
    /* margin: 0 10px; */
